
html, body {
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    //align-items: stretch;
    width: 100vw;
}

.container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    align-items: flex-start;
    padding: 30px;

    .view-points {
        //transform: rotate(-90deg);
    }

}



.photo-capture{
    position: relative;

    .frame {
        position: absolute;
        top: 40px;
        left: 20px;
        z-index: 4;
    }


    video {
        display: block;
        width: 100%;
        max-width: 400px;
        max-height: 300px;
    }
   
   .output {
       display: block;
       max-width: 100%;
   }

}
.images {
    padding: 0 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    img {
        flex: 0 0 30%;
        width: 33%;
        display: block;
        object-fit: cover;
        display: inline-block;
        margin-bottom: 10px;
    }
}


.actions {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
    width: 100%;
}

button {
    background-color:teal;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    min-width: 400px;
  }




g.selected {
    path {
        fill:green;
    }
}